.after-header {
  position: relative;
  min-width: 320px;
  background: no-repeat url("../img/bg-back-mobile@1x.jpg");
  background-size: contain;
  margin: 0;
  padding-top: 64px;
  @media (min-resolution: 1.5dppx) {
    background: no-repeat url("../img/bg-back-mobile@2x.jpg");
    background-size: contain;
    background-position: center top;
  }
}

.after-header__image {
  display: block;
  padding-top: 26px;
  position: relative;
  left: -20px;
}

.after-header__title {
  width: 273px;
  color: #ffffff;
  font-size: 24px;
  font-weight: 300;
  margin: 0;
  text-align: center;
  padding-top: 23px;
}

.after-header__button {
  .button-pink();
  display: block;
}


.after-header__button:hover {
  color: #d22856;
  background-color: #ffffff;
}

.after-header__button:active {
  color: rgba(210, 40, 86, 0.3);
  background-color: #ffffff;
}

.after-header__text {
  color: #ffffff;
  font-size: 14px;
  font-weight: 300;
  line-height: 24px;
  margin: 0;
  padding: 11px 0 29px 0;
}

.after-header__os-icon {
  margin-top: 35px;
}

.after-header__os-icon a:nth-of-type(2) {
  padding: 0 32px;
}

.after-header__get-app {
  background-color: #1d2631;
  text-align: center;
  padding: 31px 0 0 0;
}

@media (min-width: 660px) {
  .after-header {
    background: no-repeat url("../img/bg-back-tablet.jpg");
    padding-top: 64px;
    background-position-x: 50%;
    margin-top: -108px;
    padding-bottom: 382px;
    position: relative;
    overflow: hidden;
    @media (min-resolution: 1.5dppx) {
      background: no-repeat url("../img/bg-back-tablet@2x.jpg");
      background-size: 1200px 729px;
      background-position-y: 2px;
      background-position-x: -269px;
    }
  }

  .after-header__image {
    display: block;
    position: absolute;
    left: -19px;
    top: 22%;
    width: auto;
    max-width: fit-content;
  }

  .after-header__title {
    position: relative;
    width: 273px;
    color: #ffffff;
    font-size: 40px;
    font-weight: 300;
    line-height: 48px;
    margin: 0;
    text-align: center;
    padding: 139px 0 0 28px;
  }

  .after-header__get-app {
    background-color: transparent;
    text-align: center;
    position: absolute;
    left: -73px;
    top: 347px;
    z-index: 3;
  }

  .after-header__button {
    .button-pink();
    display: inline-block;
    padding: 16px 46px;
    margin-left: 47px;
  }

  .after-header__os-icon {
    margin: 44px 0 0 41px;
  }

  .after-header__text {
    padding: 26px 0 29px 47px;
    width: 401px;
    font-size: 18px;
    font-weight: 300;
    line-height: 24px;
  }
}

@media (min-width: 1200px) {
  .after-header {
    position: relative;
    background: no-repeat url("../img/bg-back-desktop.jpg");
    background-position-x: 50%;
    margin: -112px 0 0 0;
    padding-top: 64px;
    padding-bottom: 433px;
    @media (min-resolution: 1.5dppx) {
      background: no-repeat url("../img/bg-back-desktop@2x.jpg");
      background-size: 2560px 872px;
      background-position-y: 0;
      background-position-x: -679px;
    }
  }

  .after-header__title {
    font-size: 50px;
    font-weight: 30px;
    padding: 158px 0 26px 0;
    width: 300px;
    margin-left: 108px;
    line-height: 57px;
  }

  .after-header__image {
    left: 12vw;
    top: 30vh;
  }

  .after-header__button {
    .button-pink();
    display: inline-block;
    padding: 16px 30px;
    margin-left: 184px;
    font-size: 21px;
  }

  .after-header__os-icon {
    margin: 64px 0 0 178px;
  }

  .after-header__text {
    padding: 44px 0 29px 168px;
    width: 488px;
  }

  .after-header__get-app {
    top: 419px;
  }
}
