@font-face {
  font-family: "Open Sans";
  src: url(../fonts/opensans.woff2) format("woff2"), url(../fonts/opensans.woff) format("woff");
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: "Open Sans";
  src: url(../fonts/opensanslight.woff2) format("woff2"), url(../fonts/opensanslight.woff) format("woff");
  font-style: normal;
  font-weight: 300;
}
@font-face {
  font-family: "Open Sans";
  src: url(../fonts/opensansbold.woff2) format("woff2"), url(../fonts/opensansbold.woff) format("woff");
  font-style: normal;
  font-weight: 700;
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  font-size: 14px;
  line-height: 30px;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
ul {
  list-style: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0, 0, 0, 0);
}
.container {
  width: 100%;
  padding: 0 20px;
}
.container__inner {
  width: 100%;
  max-width: 320px;
  margin: 0 auto;
}
@media (min-width: 660px) {
