}
.page-header {
  background-color: #283645;
  margin-bottom: -64px;
  z-index: 2;
  position: relative;
}
.page-header--close {
  min-height: 66px;
  margin-bottom: -73px;
  z-index: 2;
  position: relative;
  background-color: rgba(0, 0, 0, 0.6);
}
.page-header--close .main-nav__toggle--close::before,
.page-header--close .main-nav__toggle--close::after {
  display: none;
}
.page-header--close .main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  top: -54px;
  border: none;
}
.page-header--close .main-nav__toggle--open {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  top: -54px;
  background: no-repeat url("../img/icon-menu-burger.svg");
  border: none;
}
.page-header--close .main-nav__toggle--open:hover {
