  width: 50px;
  height: 30px;
  right: 20px;
  top: -54px;
  background: no-repeat url("../img/icon-menu-burger-red.svg");
  border: none;
}
.page-header--close .main-nav__toggle--open:active {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  top: -54px;
  background: no-repeat url("../img/icon-menu-burger-red.svg");
  border: none;
  opacity: 0.3;
}
.page-header--close .site-list {
  display: none;
}
