.main-nav {
  position: relative;
}

.main-nav__link {
  color: #ffffff;
}

.main-nav__link:hover:not(.main-nav__link--current) {
  color: #d22856;
  cursor: pointer;
}

.main-nav__link:active:not(.main-nav__link--current) {
  opacity: 0.3;
  color: #d22856;
  cursor: pointer;
}

.main-nav__list {
  text-align: center;
  padding-left: 0;
  margin: 0;
}

.main-nav__toggle {
  position: absolute;
  width: 50px;
  height: 30px;
  right: 20px;
  top: -54px;
  border: none;

 /* &--open {
    position: absolute;
    width: 50px;
    height: 30px;
    right: 20px;
    top: -54px;
    background: no-repeat url("../img/icon-menu-burger.svg");
    border: none;

    &:hover {
      position: absolute;
      width: 50px;
      height: 30px;
      right: 20px;
      top: -54px;
      background: no-repeat url("../img/icon-menu-burger-red.svg");
      border: none;
    }

    &:active {
      position: absolute;
      width: 50px;
      height: 30px;
