      top: -54px;
      background: no-repeat url("../img/icon-menu-burger-red.svg");
      border: none;
      opacity: 0.3;
    }
  }*/

  &--close {
    position: absolute;
    width: 50px;
    height: 30px;
    right: 7px;
    top: -57px;
    background-color: @grey;
    outline: none;
    border: none;

    &::before {
      content: "";
      position: absolute;
      width: 28px;
      height: 4px;
      background-color: #ffffff;
      left: 12px;
      bottom: 11px;
      transform: rotate(135deg);
    }

    &::after {
      content: "";
      position: absolute;
      width: 28px;
      height: 4px;
      background-color: #ffffff;
      left: 12px;
      bottom: 11px;
      transform: rotate(45deg);
    }
  }
}

.main-nav__toggle--close:hover::before {
  background-color: #d22856;
}

.main-nav__toggle--close:hover::after {
  background-color: #d22856;
}

.main-nav__toggle--close:active::before {
  background-color: #d22856;
  opacity: 0.3;
}

.main-nav__toggle--close:active::after {
  background-color: #d22856;
  opacity: 0.3;
}

@media (min-width: 660px) {
  /*.main-nav__toggle {
    left: 112vh;
    top: -14vh;
  }*/
  .main-nav__toggle--close {
    position: absolute;
    right: 8px;
    top: -13vh;
    background-color: @grey;
    outline: none;
    border: none;
  }

}

@media (min-width: 1200px) {
  .main-nav__toggle--close {
    display: none;
  }

  .main-nav__link:active:not(.main-nav__link--current) {
    opacity: 0.3;
    color: #ffffff;
    cursor: pointer;
  }
}
